<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.go(-1)" />

    <van-search show-action placeholder="请输入搜索关键词" clearable v-model="searchItem">
      <template #action>
        <div @click="goSearch(searchItem)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history" v-if="history.length>0"  >
      <div class="title">
        <span>最近搜索</span>
        <van-icon name="delete-o" size="16" @click="clear" />
      </div>
      <div class="list" >
        <div v-for="e in history" :key="e" class="list-item" @click="goSearch(e)"> {{e}} </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getHistory, setHistory } from '@/utils/storage'
export default {
  name: 'SearchGoods',
  data () {
    return {
      history: getHistory(),
      searchItem: ''
    }
  },
  methods: {
    goSearch (e) {
      console.log('search', e)
      // 判断历史记录里有没有该项-如果有，先移除再添加到最前面-如果没有，添加到最前面
      const index = this.history.indexOf(e)
      if (index !== -1) {
        // 存在相同的项
        this.history.splice(index, 1)
      }
      if (e !== '') {
        this.history.unshift(e)
      }
      // 设置本地存储
      setHistory(this.history)
      // 页面传参跳转
      this.$router.push(`/searchlist?item=${e}`)
    },
    clear () {
      this.history = []
      setHistory(this.history)
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    width: 30%;
    text-align: center;
    padding: 7px;
    line-height: 15px;
    border-radius: 50px;
    background: #fff;
    font-size: 13px;
    border: 1px solid #efefef;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
